import React, { useState } from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography, Grid } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import { COLORS } from "../../helpers/constants";
import Select from "../../components/input/Select";
import crossIcon from '../../assets/svg/cross-icon.svg'

const AddUser = ({ open, onClose, data, setData, onSubmit, loading, tab, edit, selectSkill, user }) => {

  const USER_SCHEMA = [
    edit !== true && ({
      title: "Skill Name",
      placeholder: "Enter SkillName",
      type: SCHEMA_TYPES.INPUT,
      name: "skill_name",
      validator: VALIDATORS.REQUIRED,
    }),
    // {
    //   title: "Duration",
    //   placeholder: "Number of Days",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "fullname",
    //   validator: VALIDATORS.REQUIRED,
    // },
    // {
    //   title: "Performance Criteria",
    //   placeholder: "Performance Criteria",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "performance_criteria",
    //   validator: VALIDATORS.REQUIRED,
    // },
  ];

  // const addSupervisor =[
  //   {
  //     title: "Supervisor",
  //     placeholder: "Select Supervisor",
  //     type: SCHEMA_TYPES.SELECT,
  //     name: "username",
  //     multi:true,
  //     height:'auto',
  //     keyValues:[{value:'super A',key:"super A"},{value:"super B",key:"super B"},{value:"super C",key:"super C"}]
  //   }
  // ]


  const [errors, setErrors] = useState(new Map());
  const [step, setStep] = useState(0);
  const [level, setLevel] = useState(1)
  const [levels, setLevels] = useState(1)

  const handleChange = (e) => {
    const { name, value } = e.target;
    console.log("handle change----->>>", name, value)
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const handleNext = async () => {
    if (data.skill_name !== '') {
      // const _errors = Validate(USER_SCHEMA, data);
      // setErrors(_errors);
      // if (_errors.size) return;
      await onSubmit()
      // onClose()
      // setStep((prev) => prev + 1);
      return;
    }
  };


  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-5"
    >
      <h3 className="opacity-80">{edit === true ? "Edit" : "Adding"} Skill</h3>
      <div style={{ display: 'flex', gap: 5 }} className="fw-16 mb-1">
        {levels > 1 && (<Typography sx={{ cursor: 'pointer' }} onClick={() => setLevel(1)}>Level 1 &gt;</Typography>)}
        {levels > 1 && (<Typography sx={{ cursor: 'pointer' }} onClick={() => setLevel(2)}>Level 2 &gt;</Typography>)}
        {levels > 2 && (<Typography sx={{ cursor: 'pointer' }} onClick={() => setLevel(3)}>Level 3</Typography>)}
      </div>
      {/* {edit && (<h3 className="opacity-80">{data.skill_name}</h3>)} */}
      <div style={{ display: 'grid', gap: 10 }}>
        {level === 1 && (
          <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
            <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>Enter Skill Name</Typography>
            <Input name='skill_name'
              value={data.skill_name}
              onChange={handleChange}
              disabled={edit===true}
              width={"430px"} />
               <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>Add Supervisor</Typography>
                <Select
                  name={'supervisor_one'}
                  width="100%"
                  onChange={handleChange}
                  // error={errors.get(d?.name)}
                  value={data.supervisor_one}
                  keyValues={user}
                  // required={!!d?.validator}
                  multi={true}
                  height={'auto'}
                />
          </Grid>
        )}
        {level === 1 && (
          <>
            <Typography sx={{ textAlign: 'center' }} onClick={()=>console.log('supervisor',data)}>Level 1</Typography>
            <div style={{ display: 'grid', gap: 10 }}>
              {/* <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: '1fr 2fr 2fr 3fr', xs: '1fr 2fr 2fr 3fr' }}> */}
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{  sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)'  }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>Add Days</Typography>
                <Input
                  type={'number'}
                  name='days_1'
                  value={data.days_1}
                  width={"430px"}
                  onChange={handleChange}
                />  
              </Grid>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>Exam Passing Criteria</Typography>
                <Input type={'number'} name='exam_1' value={data.exam_1} onChange={handleChange} width={"430px"} />
              </Grid>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>Floor Work Criteria</Typography>
                <Input type={'number'} name='floor_1' value={data.floor_1} onChange={handleChange} width={"430px"} />
              </Grid>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>Number of Entry</Typography>
                <Input type={'number'} name='entry_1' value={data.entry_1} onChange={handleChange} width={"430px"} />
              </Grid>
            </div>
          </>
        )}
        {level === 2 && (
          <>
            <Typography sx={{ textAlign: 'center' }}>Level 2</Typography>
            <div style={{ display: 'grid', gap: 10 }}>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>Add Days</Typography>
                <Input
                  type={'number'}
                  name='days_2'
                  value={data.days_2}
                  width={"430px"}
                  onChange={handleChange}
                />
                {/* <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>Add Supervisor</Typography>
                <Select
                  name={'supervisor_two'}
                  width="100%"
                  onChange={handleChange}
                  // error={errors.get(d?.name)}
                  // value={data.supervisor_two}
                  keyValues={user}
                  // required={!!d?.validator}
                  multi={true}
                  height={'auto'}
                /> */}
              </Grid>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>Exam Passing Criteria</Typography>
                <Input type={'number'} name='exam_2' value={data.exam_2} onChange={handleChange} width={"430px"} />
              </Grid>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>Floor Work Criteria</Typography>
                <Input type={'number'} name='floor_2' value={data.floor_2} onChange={handleChange} width={"430px"} />
              </Grid>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>Number of Entry</Typography>
                <Input type={'number'} name='entry_2' value={data.entry_2} onChange={handleChange} width={"430px"} />
              </Grid>
            </div>
          </>
        )}
        {level === 3 && (
          <>
            <Typography sx={{ textAlign: 'center' }}>Level 3</Typography>
            <div style={{ display: 'grid', gap: 10 }}>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)'}}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>Add Days</Typography>
                <Input
                  type={'number'}
                  name='days_3'
                  value={data.days_3}
                  width={"430px"}
                  onChange={handleChange}
                />
                {/* <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>Add Supervisor</Typography>
                <Select
                  name={'supervisor_three'}
                  width="100%"
                  onChange={handleChange}
                  // error={errors.get(d?.name)}
                  // value={data.supervisor_three}
                  keyValues={user}
                  // required={!!d?.validator}
                  multi={true}
                  height={'auto'}
                /> */}
              </Grid>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>Exam Passing Criteria</Typography>
                <Input type={'number'} name='exam_3' value={data.exam_3} onChange={handleChange} width={"430px"} />
              </Grid>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>Floor Work Criteria</Typography>
                <Input type={'number'} name='floor_3' value={data.floor_3} onChange={handleChange} width={"430px"} />
              </Grid>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>Number of Entry</Typography>
                <Input type={'number'} name='entry_3' value={data.entry_3} onChange={handleChange} width={"430px"} />
              </Grid>
            </div>
          </>
        )}
      </div>
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
        px={8}
      >
        <Button bordered color={'light_green'} disabled={level === 3} onClick={() => {
          level <= 3 && setLevel((prev) => prev + 1);
          levels <= 3 && setLevels((prev) => prev + 1)
        }
        }>
          <Typography color={COLORS.TEXT_GRAY}>{edit===true?'Next':'Add Level'}</Typography>
        </Button>
        <Button color={'light_green'} onClick={handleNext} loading={loading}>
          <Typography color={COLORS.BLACK}>Submit</Typography>
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddUser;
